define('wge-examples/controllers/components/index', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Controller.extend({
    value: 'ok',
    actions: {
      accept: function accept() {
        alert('accepted');
      },
      cancel: function cancel() {
        alert('cancelled');
      }
    }
  });

});